/* z-index */
.mint-indexlist {
    width: 100%;
    position: relative;
    overflow: hidden
}
.mint-indexlist-content {
    margin: 0;
    padding: 0;
    overflow: auto
}
.mint-indexlist-nav {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    margin: 0;
    background-color: #fff;
    border-left: solid 1px #ddd;
    text-align: center;
    max-height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center
}
.mint-indexlist-navlist {
    padding: 0;
    margin: 0;
    list-style: none;
    max-height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column
}
.mint-indexlist-navitem {
    padding: 2px 6px;
    font-size: 12px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-touch-callout: none
}
.mint-indexlist-indicator {
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    line-height: 50px;
    background-color: rgba(0, 0, 0, .7);
    border-radius: 5px;
    color: #fff;
    font-size: 22px
}
.mint-indexsection {
    padding: 0;
    margin: 0
}
.mint-indexsection-index {
    margin: 0;
    padding: 10px;
    background-color: #fafafa
}
.mint-indexsection-index + ul {
    padding: 0
}
.mint-palette-button{
  display:inline-block;
  position:relative;
  border-radius:50%;
  width: 56px;
  height:56px;
  line-height:56px;
  text-align:center;
  -webkit-transition:-webkit-transform .1s ease-in-out;
  transition:-webkit-transform .1s ease-in-out;
  transition:transform .1s ease-in-out;
  transition:transform .1s ease-in-out, -webkit-transform .1s ease-in-out;
}
.mint-main-button{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  border-radius:50%;
  background-color:blue;
  font-size:2em;
}
.mint-palette-button-active{
  -webkit-animation: mint-zoom 0.5s ease-in-out;
          animation: mint-zoom 0.5s ease-in-out;
}
.mint-sub-button-container>*{
  position:absolute;
  top:15px;
  left:15px;
  width:25px;
  height:25px;
  -webkit-transition:-webkit-transform .3s ease-in-out;
  transition:-webkit-transform .3s ease-in-out;
  transition:transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
}
@-webkit-keyframes mint-zoom{
0% {-webkit-transform:scale(1);transform:scale(1)
}
10% {-webkit-transform:scale(1.1);transform:scale(1.1)
}
30% {-webkit-transform:scale(0.9);transform:scale(0.9)
}
50% {-webkit-transform:scale(1.05);transform:scale(1.05)
}
70% {-webkit-transform:scale(0.95);transform:scale(0.95)
}
90% {-webkit-transform:scale(1.01);transform:scale(1.01)
}
100% {-webkit-transform:scale(1);transform:scale(1)
}
}
@keyframes mint-zoom{
0% {-webkit-transform:scale(1);transform:scale(1)
}
10% {-webkit-transform:scale(1.1);transform:scale(1.1)
}
30% {-webkit-transform:scale(0.9);transform:scale(0.9)
}
50% {-webkit-transform:scale(1.05);transform:scale(1.05)
}
70% {-webkit-transform:scale(0.95);transform:scale(0.95)
}
90% {-webkit-transform:scale(1.01);transform:scale(1.01)
}
100% {-webkit-transform:scale(1);transform:scale(1)
}
}
@font-face {font-family: "mintui";
  src: url(data:application/x-font-ttf;base64,)
}
.mintui {
  font-family:"mintui" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.mintui-search:before { content: "\E604"; }
.mintui-more:before { content: "\E601"; }
.mintui-back:before { content: "\E600"; }
.mintui-field-error:before { content: "\E605"; }
.mintui-field-warning:before { content: "\E608"; }
.mintui-success:before { content: "\E602"; }
.mintui-field-success:before { content: "\E609"; }
